// import Rili from "../common/dark-calendar/dark-calendar.vue"
export default{
	components:{

	},
	data(){
		return{
			result:{},
			type:0,//1线路  2：平台
			content:"",
			id:null,
			childInfo:{},
			lineList:[],
			lineId:'',
			parentId:null
		}
	},
	onLoad(e){
		this.id=e.id
		this.getChildMsg()
		this.getLines()
		let userInfo=uni.getStorageSync("userInfo")
		userInfo=JSON.parse(userInfo)
		this.parentId=userInfo.id
	},
	mounted(){
		// this.$refs.calendar.show();
	},
	methods:{
		getResult(val){
			console.log(val)
			// this.result=val;
			// this.$refs.calendar.show();
		},
		getChildMsg(){
			this.$http.post("puchildren/detail",{
				id:this.id
			}).then(res=>{
				if(res.code==100){
					this.childInfo=res.info
				}
			})
		},
		getLines(){
			this.$http.post("puline/getLineByCharendId",{
				childrenId:this.id
			}).then(res=>{
				if(res.code==100){
					this.lineList=res.info
				}
			})
		},
		radioChange(val){
			console.log(val)
			this.lineId=val.detail.value
		},
		submitCompalint(){
			// 投诉
			if(!this.type){
				uni.showToast({
					icon:"none",
					title:"请选择投诉的类型"
				})
			}else if(!this.content){
				uni.showToast({
					icon:"none",
					title:"请填写投诉的内容"
				})
			}else if(this.type==1&&!this.lineId){
				uni.showToast({
					icon:"none",
					title:"请选择线路"
				})
			}else{
				this.$http.post("pucomplaint/add",{
					childrenId:this.id,
					type:this.type,
					content:this.content,
					parentId:this.parentId,
					lineId:this.lineId
				}).then(res=>{
					if(res.code==100){
						uni.showToast({
							icon:"success",
							title:"提交成功！"
						})
						setTimeout(()=>{
							uni.navigateBack({
								
							})
						},2000)
					}
				})
			}
		},
	}
}
